import React from "react";
import {
  BrowserRouter,
  Routes as Switch,
  Route,
  Navigate,
} from "react-router-dom";
import App from "../App";
import Celebrate from "../pages/celebrate";

export const Routes = () => {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/" element={<App />} />
        <Route path="/celebrate" element={<Celebrate />} />
        <Route path="/:unknown" element={<Navigate replace to="/" />} />
      </Switch>
    </BrowserRouter>
  );
};
