import { data } from "./story";
import "./App.css";
import { Collapse, Typography } from "antd";
import { MyMenu } from "./component/menu";
const { Panel } = Collapse;
const { Title } = Typography;

function App() {
  return (
    <div className="App">
      <MyMenu defaultSelectedKeys={["1"]} />
      <Title level={5} style={{ paddingTop: "10px" }}>
        website for bedtime story.have a good night!
      </Title>
      <Collapse defaultActiveKey={data[0].time} accordion={true}>
        {data.map((item) => (
          <Panel header={item.time} key={item.time}>
            <div dangerouslySetInnerHTML={{ __html: item.story }} />
          </Panel>
        ))}
      </Collapse>
    </div>
  );
}

export default App;
